.App {
  text-align: center;
  height: 100vh; /* Set height to 100% of viewport height */
  background-repeat: no-repeat;
  background-size: cover;
  padding: 50px; 
}
.para{
  font-size: larger;
  font-weight: bolder;
  color: rgb(245, 139, 0);
}
.head {
  margin-top: 10px;
  margin-bottom: 0px;
  margin-right: 10000px;
  font-size: 28px;
  color: #01f7fb;
}

.App button {
  display: inline-block; /* Use inline-block to allow margin properties to work */
  margin: 10px; /* Adjust margins as needed */
  padding: 6px 12px; /* Add padding to the top and bottom */
}

.content {
  margin-bottom: 10px;
}

.sun {
  background-image: url('./images/sunny.jpg');
}

.cloud {
  background-image: url('./images/cloud.jpg');
}

.mist {
  background-image: url('./images/mist.jpg');
}

.clear {
  background-image: url('./images/clearsky.jpg');
}

.defa {
  background-image: url('./images/defa.jpg');
}

/* Hide elements with .disn class */
.disn {
  display: none;
}
